<template>
    <div
        :style="{
            display: 'flex',
            justifyContent: justify,
            alignItems: align,
            flexDirection: vertical ? 'column' : 'row',
            gap: gap,
        }"
    >
        <slot />
    </div>
</template>

<script>
export default {
    props: {
        vertical: {
            type: Boolean,
            default: false,
        },
        align: {
            type: String,
            default: "center",
        },
        justify: {
            type: String,
            default: "center",
        },
        gap: {
            type: String,
            default: "20px",
        },
    },
};
</script>