<template>
    <MySpace vertical gap="0">
        <h1>这是一个互动视频的测试</h1>
        <MySpace justify="space-between" :style="{ width: '600px' }">
            <h3>视频编号 {{ num }}</h3>
            <MySpace>
                <h3>视频路径</h3>
                <h3 v-for="(p, i) in path" :key="i">{{ p }}</h3>
            </MySpace>
        </MySpace>

        <MyTest :src="videos[num].src" :p="videos[num].p" />

        <h3>{{ videos[num].desc ? videos[num].desc : "你的选择是" }}</h3>
        <MySpace>
            <button
                v-for="(option, i) in videos[num].options"
                :key="i"
                @click="(num = option.jump), append(option.jump)"
            >
                {{ option.text }}
            </button>
        </MySpace>
    </MySpace>
</template>

<script>
import MyTest from "./components/MyTest.vue";
import MySpace from "./components/MySpace.vue";

export default {
    name: "App",
    components: {
        MyTest,
        MySpace,
    },
    data() {
        return {
            num: 0,
            path: [0],
            videos: [
                {
                    src: "BV1kv411P7Sz",
                    desc: "你愿意成为伥鬼吗",
                    options: [
                        {
                            text: "我就是伥鬼本鬼",
                            jump: 1,
                        },
                        {
                            text: "什么是伥鬼",
                            jump: 2,
                        },
                    ],
                },
                {
                    src: "BV15A411c7nc",
                    p: 11,
                    desc: "结局一",
                    options: [
                        {
                            text: "回到开始",
                            jump: 0,
                        },
                    ],
                },
                {
                    src: "BV1ha411672q",
                    options: [
                        {
                            text: "去看看伥鬼现场",
                            jump: 1,
                        },
                    ],
                },
            ],
        };
    },
    methods: {
        append(num) {
            if (this.path.indexOf(num) >= 0) {
                let i = this.path.indexOf(num);
                this.path.splice(i + 1, this.path.length - i - 1);
            } else {
                this.path.push(num);
            }
        },
    },
};
</script>

